@import url("https://fonts.googleapis.com/css?family=Josefin+Sans:300,400");
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

#list {
  font-size: .6em;
}

/* Heading */
.title-1 {
  font-size: 0.5em;
}

.title-2 {
  font-size: 1em;
}

.title-3 {
  font-size: 1.5em;
}

.title-4 {
  font-size: 2em;
}

.title-5 {
  font-size: 2.5em;
}

.title-6 {
  font-size: 3em;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 400;
  color: #0b1d51;
}

body {
  font-family: 'Josefin Sans', sans-serif;
  font-size: 25px;
  font-weight: 300;
  margin: 0;
  padding: 0;
}

header {
  color: #0b1d51;
  margin: 0;
  padding: 0;
  border-bottom: .5em solid #797596;
}

aside {
  color: #d1c6ad;
}

article {
  color: #a1869e;
}

footer {
  color: #797596;
}

#vertical {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: distribute;
      align-content: space-around;
}
.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: distribute;
      align-content: space-around;
}

/* Grid */
body {
  height: 100%;
  display: grid;
  grid-gap: 20px;
  grid-auto-flow: column;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  grid-template-areas: "header header header" "sidebar content content" "sidebar content content" "footer footer footer";
}
header {
  grid-area: header;
  text-align: center;
}

article {
  grid-area: content;
}

footer {
  grid-area: footer;
  text-align: center;
  background: #EEE;
  -ms-flex-item-align: end;
      align-self: end;
}

/* Navigation */
.top ul {
  list-style: none;
  text-align: center;
  padding: 14px 16px;
  margin: 0;
}

.top li {
  font-size: 1.2em;
  line-height: 40px;
  height: 40px;
  border-bottom: 1px solid #888;
}

.top a {
  text-decoration: none;
  display: block;
  color: #a1869e;
  -webkit-transition: .3s background-color;
  -o-transition: .3s background-color;
  transition: .3s background-color;
}

.top li {
  display: inline-block;
  margin-right: -4px;
  padding: 8px;
}

nav {
  margin: 0;
  padding: 0;
}

.top a:hover {
  color: #797596;
  background: #CCC;
}



